html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

@font-face {
  src: url(/the-trivia/static/media/VT323-Regular.d55abc3c.ttf);
  font-family: "VT323";
}

:root {
  --main-bg-color: rgb(3,30,17);
  --main-font-color: rgb(18, 218, 64);
}

input:focus, button:focus {
  outline: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: "VT323";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(3,30,17);
  background-color: var(--main-bg-color);
  color: rgb(18, 218, 64);
  color: var(--main-font-color);
  font-size: 40px;
}

/* HOMEPAGE */
.homepage {
  padding: 40px 80px;
}

.homepage h2 {
  margin-bottom: 40px;
}

.homepage a {
  text-decoration: none;
  color: rgb(18, 218, 64);
  color: var(--main-font-color);
  white-space: nowrap;
}

.homepage a:focus, .homepage a:hover {
  outline: none;
  background-color: rgb(18, 218, 64);
  box-shadow: 0 0 5px #22ff22, 0 0 5px #aaffaa;
  color: #1b3a22;
  text-shadow: none;
}

.homepage a:before {
  content:">";
  margin-right: 10px;
  visibility: hidden;
}

.homepage a:hover:before, .homepage a:focus:before {
  visibility: visible;
  /* animation: blinking 0.8s infinite step-start; */
}

.homepage .lastCatIndication {
  margin-left: 20px;
  color: rgb(255,255,50);
  -webkit-animation: blinking 1s infinite step-start;
          animation: blinking 1s infinite step-start;
}

.homepage button {
  border: none;
  background: transparent;
  color: rgb(18, 218, 64);
  color: var(--main-font-color);
  font-family: "VT323";
  font-size: 30px;
  margin-top: 50px;
  cursor: pointer;
}

/* CATEGORY */
.category {
  padding: 40px 80px;
}

.category .back {
  display: block;
  text-decoration: none;
  color: rgb(18, 218, 64);
  color: var(--main-font-color);
  white-space: nowrap;
  margin-bottom: 40px;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  -webkit-transform: translateX(-30px);
          transform: translateX(-30px);
}

.category .back:before {
  content:"<";
  margin-right: 10px;
  visibility: hidden;
}

.category .back:hover:before {
  content:"<";
  margin-right: 10px;
  visibility: visible;
}

.category h3 {
  margin-bottom: 10px;
}

.category .score {
  margin-bottom: 40px
}

.category .question {
  margin-bottom: 40px;
}

.category .question h4{
  margin-bottom: 10px;
}

.category .answer {
  white-space: nowrap;
  position: relative;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
  margin-bottom: 20px;
}

.category .answerInput{
  font-family: "VT323";
  border: none;
  color: rgb(18, 218, 64);
  color: var(--main-font-color);
  font-size: 40px;
  background-color: transparent;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
}

.category .answer span:before {
  content: "=>";
  margin-right: 10px;
}

.category .answerResult {
  margin-bottom: 40px;
}

.category button {
  border: none;
  background-color: transparent;
  color: rgb(18, 218, 64);
  color: var(--main-font-color);
  font-family: "VT323";
  font-size: 40px;
  cursor: pointer;
}

.category .nextQuestion {
  margin: 20px;
}

.homepage .moreCatButton {
  color: rgb(255,50,50);
}

.moreCatButton:hover {
  background-color: rgb(255,50,50);
  box-shadow: 0 0 5px rgb(221, 41, 41), 0 0 5px rgb(255, 84, 84);
  color: #1b3a22;
  text-shadow: none;
}

.nextQuestion:hover {
  /* height: 16px;
  line-height: 12px; */
  background-color: rgb(18, 218, 64);
  box-shadow: 0 0 5px #22ff22, 0 0 5px #aaffaa;
  color: #1b3a22;
  text-shadow: none;
  }

.category .reset {
  margin-top: 40px;
  -webkit-transform: translateX(-30px);
          transform: translateX(-30px);
}

.category a:focus, .category a:hover, .category button:focus, .category button:hover {
  outline: none;
  background-color: rgb(18, 218, 64);
  box-shadow: 0 0 5px #22ff22, 0 0 5px #aaffaa;
  color: #1b3a22;
  text-shadow: none;
}

@-webkit-keyframes blinking {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes blinking {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

/* LE BEAU CSS DE QUENTIN L'ENGIN */

.crt, .crt button, .crt input {
  -webkit-animation: textShadow 1.6s infinite;
          animation: textShadow 1.6s infinite;
}


.crt::before {
  content: " ";
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: linear-gradient(rgba(18, 16, 16, 0) 50%, rgba(0, 0, 0, 0.25) 50%), linear-gradient(90deg, rgba(255, 0, 0, 0.06), rgba(0, 255, 0, 0.02), rgba(0, 0, 255, 0.06));
  z-index: 2;
  background-size: 100% 2px, 3px 100%;
  pointer-events: none;
}

.crt::after {
  content: " ";
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(18, 16, 16, 0.1);
  opacity: 0;
  z-index: 2;
  pointer-events: none;
  -webkit-animation: flicker 0.15s infinite;
          animation: flicker 0.15s infinite;
}

